<template>
    <div class="NavBar">
        <div class="top_box">
            <div class="top">
                <img src="~@/assets/logo.png" alt="">
                <div class="nav_box">
                    <ul class="nav clearfloat">
                        <li class="nav-list" @click="goPage(1)">首页</li>
                        <li class="nav-list" @click="goPage(2)">简介</li>
                        <li class="nav-list" @click="goPage(3)">活动</li>
                        <li class="nav-list" @click="goPage(4)">字画</li>
                        <li class="nav-list last" @click="goPage(5)">书影</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
  export default {
    methods: {
        goPage(type){
            if(type == 1){
                this.$router.push({path:'/Home'});
            }
            else if(type == 2){
                 //this.$router.push({path:'/Intro'});
            }
            else if(type == 3){
                 //this.$router.push({path:'/Activity'});
            }
            else if(type == 4){
                // this.$router.push({path:'/Acripts'});
            }
            else if(type == 5){
                 //this.$router.push({path:'/Book'});
            }
        }
    },
  }
</script>
<style lang="less" scoped>
.NavBar{
    margin-bottom: 20px;
}
 .top_box{
     background: #79242F;
 }
 .top{
     padding: 47/2px 30/2px;
     width: 1170px;
     margin:  0 auto;
     text-align: center;
     img{
         height:180/2px;
         margin-bottom: 10px;
     }
 }
 .nav_box{
      border-top: 1px solid #EFCAA9;
    border-bottom: 1px solid #EFCAA9;
    text-align: center;
 }
.nav{
 display:inline-block;
}
.nav-list{
     padding: 10px 0;
    font-size: 38/2px;
    color: #EFCAA9;
    float: left;
    margin-right: 128px;
    cursor: pointer;
}
.last{
    margin-right: 0;
}
</style>


